<template>
  <div>
    <h1>{{video.titulo}}</h1>
    <div class="bloque__article__img">
      <iframe width="450" height="315"
      :src="video.urlVideo">
      </iframe>
    </div>
    <div class="bloque__article__mainInfo">
      <p class="title">{{video.titulo}}</p>
      <p class="desc">{{video.descripcion}}</p>
    </div>
  </div>
</template>

<script>
export default {
    data(){
      return{
        video:{}
      }
    },
    methods:{
      async getVideo(){
        const info = await fetch(`http://localhost:3000/posts/${this.$route.params.id}`);
        const data = await info.json();

        this.video = data;

        console.log(data);
      }
    },

    created(){
      this.getVideo();
    }
}
</script>

